import { breakNumber, numberToStar, numberMeaning, stars, combo, alphaToNumber, numberToGua, nameChinese } from '../helpers/NumberEnergy'
import { Form, Input2 } from "../components/search-popup/style";
import Trigram from '../components/IChing/Trigram'
import Hexagram from '../cm-lib/IChing/Hexagram.json'
const { useCallback } = React

const displayTick = (tick) => tick ? <i className="icofont-tick-boxed" /> : <i className="icofont-error" /> 


const { useState, useEffect } = React
const IndexPage = ({ location }) => {
    const params = new URLSearchParams(location.search);
    const number = params.get("number");
    const [selectedDate, setSelectedDate] = useState(new Date())
    const title = 'Yi Jing Numberology';
    const description = '';
    const [inputNumber, setInputNumber] = useState(number || '')
    const [numberStars, setNumberStars] = useState([]);
    const [analysis, setAnalysis] = useState([])
    const [guaAnalysis, setGuaAnalysis] = useState(null);
    const [gender, setGender] = useState('m')

    const inputForm = useCallback((inputElement) => {
        if (inputElement) {
          inputElement.focus();
        }
      }, []);

    useEffect(() => {
        const _numberStars = breakNumber(inputNumber).map(num => ({
            number: num,
            star: numberToStar(num)
        }))
        setNumberStars(_numberStars)
        if (inputNumber?.length > 1) {
            const kuaBinary = numberToGua(inputNumber);
            const { main, process, outcome } = kuaBinary;
            setGuaAnalysis(kuaBinary)
        } else {
            setGuaAnalysis(null)
        }
    }, [inputNumber])

    useEffect(() => {
        const _analysis = []
        if (inputNumber.includes('0')) {
            _analysis.push('Avoid 0 in the number. 避开0号')
        }

        const avoidForLadies = !!['87', '78', '91', '19'].filter(n => inputNumber.includes(n)).length
        if (avoidForLadies) {
            _analysis.push('Avoid 87, 91, 19, 78 for ladies. 87, 91, 19, 79 女生不宜')
        }
        numberStars.forEach((star) => {

        }, [])

        const lastStar = numberStars[numberStars.length - 1];
        if (lastStar) {
            if (['wugui', 'liusha', 'huohai', 'jueming'].includes(lastStar.star.type)) {
                _analysis.push(`Avoid ${lastStar.star.type} at the end. 避开${nameChinese[lastStar.star.type]}尾数`)
            }
        }

        numberStars.map(e => e.type)

        setAnalysis(_analysis)
    }, [numberStars])

    const hasBadLastNumber = () => ['wugui', 'liusha', 'huohai', 'jueming'].includes(numberStars[numberStars.length - 1])
    const hasTianYi = () => numberStars.find(e => e.star.type === 'tianyi')
    const hasYanNian = () => numberStars.find(e => e.star.type === 'yannian')
    const hasShengQi = () => numberStars.find(e => e.star.type === 'shengqi')

    const breadcrumbs = [
        { to: '', text: 'Home' },
        { to: 'dashboards', text: 'Dashboards' },
    ];

    const checkAdminAccess = () => {
        return true
    }

    const [compassFacing, setCompassFacing] = useState(TWENTY_FOUR_MOUNTAIN[13]);
    const [blob, setBlob] = useState(null);

    const onUpload = (fileWithMeta, status) => {
        const { file } = fileWithMeta;
        const reader = new FileReader()
        reader.onload = () => {
            setBlob(btoa(reader.result))
            //dispatch(setContent(null))
        }
        reader.readAsBinaryString(file)
    }

    return (
        <Layout>
            <SEO title="Home" pathname="/" />
            <Container>
                <h4>
                    Here is a free tool to quickly evaluate the luck of your mobile number. It is based on few popular system to qualify your mobile number. 
                </h4>

                <br />
                <h3>
                    Analyse Your Number 分析你的号码
                </h3>
                <Form action="#">
                    <Input2 type="text" placeholder="Type in your mobile number 输入号码"
                        onChange={(e) => setInputNumber(e.target.value)}
                        value={inputNumber}
                        ref={inputForm}
                        type="number"
                    />
                    <ButtonGroup aria-label="Basic example" className="w-100 mt-3">
                        <Button onClick={() => setGender('m')} variant={gender === 'm' ? 'success' : 'secondary'}>Male 男</Button>
                        <Button onClick={() => setGender('f')} variant={gender === 'f' ? 'success' : 'secondary'}>Female 女</Button>
                    </ButtonGroup>
                </Form>

                <br />
                <br />
                {

                    guaAnalysis &&
                    <Accordion defaultActiveKey="0">
                        <Card>
                            <Card.Header>
                                <Accordion.Toggle eventKey="0">
                                    <h3>Checklist For Good Number</h3>
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                                <Card.Body>
                                    <ol>
                                        <li>Avoid Zero  { displayTick(!inputNumber.includes('0')) }</li>
                                        <li>Avoid Negative Number at the last { displayTick(!hasBadLastNumber()) } </li>
                                        <li>本卦 should be Good  { displayTick(guaAnalysis?.main?.info === 'Good') } </li>
                                        <li>互卦 should be Good  { displayTick(guaAnalysis?.process?.info === 'Good') }</li>
                                        <li>变卦 should be Good  { displayTick(guaAnalysis?.outcome?.info === 'Good') }</li>
                                        <li>Have TianYi  { displayTick(hasTianYi()) }</li>
                                        <li>Have ShengQi  { displayTick(hasShengQi()) }</li>
                                        <li>Have YanNian  { displayTick(hasYanNian()) }</li>
                                        {
                                            gender === 'f' && <li>
                                                Avoid 91, 19, 87, 78 { displayTick(!['87', '78', '91', '19'].filter(n => inputNumber.includes(n)).length)}
                                            </li>
                                        }
                                    </ol>
                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                                <Accordion.Toggle eventKey="1">
                                    <h3>Number Energy Flow 数字能量流动</h3>
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                                <Card.Body>
                                    <>

                                        {
                                            numberStars?.map(e => `${e.star.type} ${nameChinese[e.star.type]} ${e.star.level}`).join(' => ')
                                        }

                                    </>



                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>


                        <Card>
                            <Card.Header>
                                <Accordion.Toggle eventKey="2">
                                    <h3>Number Gua 数字卦</h3>
                                </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="2">
                                <Card.Body>
                                    <Row>
                                        <Col xs={3}>
                                            <Trigram
                                                trigram={guaAnalysis?.main.binary}
                                                title={`本卦 ${checkAdminAccess() && guaAnalysis?.main?.info}`}
                                            />
                                        </Col>
                                        <Col xs={1}></Col>
                                        <Col xs={3}>

                                            <Trigram
                                                trigram={guaAnalysis?.process.binary}
                                                title={`互卦 ${checkAdminAccess() && guaAnalysis?.process?.info}`}
                                            />
                                        </Col>
                                        <Col xs={1}></Col>
                                        <Col xs={3}>
                                            <Trigram
                                                trigram={guaAnalysis?.outcome.binary}
                                                title={`变卦 ${checkAdminAccess() && guaAnalysis?.outcome?.info}`}

                                            />
                                        </Col>
                                    </Row>




                                </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                }

            </Container>

        </Layout>
    );
};

export default IndexPage;
